<template>
  <div class="model-config">
    <h1>
      模型配置管理
      <div class="header-buttons">
        <el-button type="default" size="small" icon="el-icon-back" @click="goBack">返回管理面板</el-button>
        <el-button type="primary" size="small" icon="el-icon-s-home" @click="goHome">返回主页</el-button>
      </div>
    </h1>
    
    <el-card class="config-card">
      <div slot="header" class="card-header">
        <span>已配置模型列表</span>
        <div class="header-buttons">
          <el-button type="danger" size="small" :disabled="!selectedModels.length" @click="batchDeleteModels">批量删除</el-button>
          <el-button type="primary" size="small" @click="addNewModel">添加新模型</el-button>
        </div>
      </div>
      
      <el-table 
        :data="modelsList" 
        style="width: 100%"
        @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column prop="name" label="模型名称" width="180"></el-table-column>
        <el-table-column prop="version" label="版本" width="120"></el-table-column>
        <el-table-column prop="provider" label="提供商" width="150"></el-table-column>
        <el-table-column prop="status" label="状态">
          <template slot-scope="scope">
            <el-tag :type="scope.row.status === '已启用' ? 'success' : 'danger'">
              {{ scope.row.status }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="220">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" @click="editModel(scope.row)">编辑</el-button>
            <el-button 
              size="mini" 
              :type="scope.row.status === '已启用' ? 'danger' : 'success'"
              @click="toggleModelStatus(scope.row)"
            >
              {{ scope.row.status === '已启用' ? '禁用' : '启用' }}
            </el-button>
            <el-button size="mini" type="danger" @click="deleteModel(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    
    <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" width="50%">
      <el-form :model="modelForm" label-width="120px" :rules="rules" ref="modelForm">
        <el-form-item label="模型名称" prop="name">
          <el-input v-model="modelForm.name"></el-input>
        </el-form-item>
        <el-form-item label="版本" prop="version">
          <el-input v-model="modelForm.version"></el-input>
        </el-form-item>
        <el-form-item label="提供商" prop="provider">
          <el-select v-model="modelForm.provider" placeholder="请选择提供商">
            <el-option label="OpenAI" value="OpenAI"></el-option>
            <el-option label="百度" value="百度"></el-option>
            <el-option label="讯飞" value="讯飞"></el-option>
            <el-option label="阿里云" value="阿里云"></el-option>
            <el-option label="腾讯云" value="腾讯云"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="模型状态">
          <el-switch
            v-model="modelForm.isEnabled"
            active-text="已启用"
            inactive-text="已禁用"
            active-color="#13ce66"
            inactive-color="#ff4949">
          </el-switch>
        </el-form-item>
        <el-form-item label="API密钥" prop="apiKey">
          <el-input v-model="modelForm.apiKey" show-password></el-input>
        </el-form-item>
        <el-form-item label="参数配置">
          <el-input type="textarea" v-model="modelForm.parameters" :rows="5" placeholder="请输入模型参数配置 (JSON格式)"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取消</el-button>
        <el-button type="primary" @click="saveModel">保存</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'ModelConfig',
  data() {
    return {
      modelsList: [
        { id: 1, name: 'Yinlang 2.5', version: '2.5 Pro', provider: 'OpenAI', status: '已启用' },
        { id: 2, name: 'ChatGLM', version: '3.0', provider: '智谱AI', status: '已启用' },
        { id: 3, name: 'Llama', version: '2.0', provider: 'Meta', status: '已禁用' },
        { id: 4, name: 'Claude', version: '3.0', provider: 'Anthropic', status: '已启用' },
      ],
      dialogVisible: false,
      dialogTitle: '添加新模型',
      isEditing: false,
      currentId: null,
      modelForm: {
        name: '',
        version: '',
        provider: '',
        isEnabled: true,
        apiKey: '',
        parameters: ''
      },
      rules: {
        name: [{ required: true, message: '请输入模型名称', trigger: 'blur' }],
        version: [{ required: true, message: '请输入版本号', trigger: 'blur' }],
        provider: [{ required: true, message: '请选择提供商', trigger: 'change' }],
        apiKey: [{ required: true, message: '请输入API密钥', trigger: 'blur' }]
      },
      selectedModels: []
    }
  },
  methods: {
    addNewModel() {
      this.isEditing = false;
      this.dialogTitle = '添加新模型';
      this.resetForm();
      this.dialogVisible = true;
    },
    editModel(model) {
      this.isEditing = true;
      this.currentId = model.id;
      this.dialogTitle = '编辑模型';
      this.modelForm = {
        name: model.name,
        version: model.version,
        provider: model.provider,
        isEnabled: model.status === '已启用',
        apiKey: '********', // 通常会从API获取实际密钥
        parameters: '{\n  "temperature": 0.7,\n  "max_tokens": 2048,\n  "top_p": 0.95\n}'
      };
      this.dialogVisible = true;
    },
    toggleModelStatus(model) {
      const newStatus = model.status === '已启用' ? '已禁用' : '已启用';
      
      // 在实际应用中，这里会调用API来更新模型状态
      const index = this.modelsList.findIndex(m => m.id === model.id);
      if (index !== -1) {
        this.$set(this.modelsList, index, { ...model, status: newStatus });
        this.$message({
          type: 'success',
          message: `已${newStatus === '已启用' ? '启用' : '禁用'}模型 ${model.name}`
        });
      }
    },
    deleteModel(model) {
      this.$confirm(`此操作将永久删除模型 ${model.name}，是否继续?`, '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        // 在实际应用中，这里会调用API来删除模型
        this.modelsList = this.modelsList.filter(m => m.id !== model.id);
        this.$message({
          type: 'success',
          message: '删除成功!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });          
      });
    },
    saveModel() {
      this.$refs.modelForm.validate((valid) => {
        if (valid) {
          if (this.isEditing) {
            // 更新现有模型
            const index = this.modelsList.findIndex(m => m.id === this.currentId);
            if (index !== -1) {
              this.$set(this.modelsList, index, {
                id: this.currentId,
                name: this.modelForm.name,
                version: this.modelForm.version,
                provider: this.modelForm.provider,
                status: this.modelForm.isEnabled ? '已启用' : '已禁用'
              });
              this.$message({
                type: 'success',
                message: '模型更新成功'
              });
            }
          } else {
            // 添加新模型
            const newId = Math.max(...this.modelsList.map(m => m.id), 0) + 1;
            this.modelsList.push({
              id: newId,
              name: this.modelForm.name,
              version: this.modelForm.version,
              provider: this.modelForm.provider,
              status: this.modelForm.isEnabled ? '已启用' : '已禁用'
            });
            this.$message({
              type: 'success',
              message: '模型添加成功'
            });
          }
          this.dialogVisible = false;
        } else {
          return false;
        }
      });
    },
    resetForm() {
      this.modelForm = {
        name: '',
        version: '',
        provider: '',
        isEnabled: true,
        apiKey: '',
        parameters: '{\n  "temperature": 0.7,\n  "max_tokens": 2048,\n  "top_p": 0.95\n}'
      };
      if (this.$refs.modelForm) {
        this.$refs.modelForm.resetFields();
      }
    },
    goBack() {
      if (this.$route.path !== '/admin') {
        this.$router.push('/admin');
      }
    },
    goHome() {
      if (this.$route.path !== '/') {
        this.$router.push('/');
      }
    },
    handleSelectionChange(val) {
      this.selectedModels = val;
    },
    batchDeleteModels() {
      if (this.selectedModels.length === 0) {
        this.$message.warning('请选择要删除的模型');
        return;
      }
      
      this.$confirm(`此操作将永久删除选中的 ${this.selectedModels.length} 个模型，是否继续?`, '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const deleteIds = this.selectedModels.map(model => model.id);
        this.modelsList = this.modelsList.filter(model => !deleteIds.includes(model.id));
        this.$message({
          type: 'success',
          message: '批量删除成功!'
        });
        this.selectedModels = [];
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    }
  }
}
</script>

